@import 'antd/lib/style/themes/default.less';
@import 'antd/dist/antd.dark.less';
@import 'antd/dist/antd.compact.less';
@import 'ol/ol.css';

@card-background: transparent;

.ant-layout-header {
  color: white;
}
.ant-layout-header a {
  color: white;
}

.ant-layout-sider-children {
  overflow-y: auto;
}

.ant-menu-item {
  height: 100%;
}

.ant-menu-submenu-title {
  font-size: 'medium';
}

.ol-overviewmap-box {
  border: 2px solid @primary-color !important;
}

img {
  object-fit: contain;
  max-height: 100%;
}

@layout-header-background: #3C2C59;@primary-color: #3C2C59;@processing-color: #8cb8c6;@success-color: #3f9c35;@warning-color: #eeaf30;@error-color: #96172e;@font-size-base: 14px;